import cmdProgress from "@/components/cmd-progress/cmd-progress.vue"
export default {
	components: {
		cmdProgress,
	},
	data() {
		return {
			id: '2c9a29b679c688a20179c6914a1a01e6', // 答案id
			collectsId: '702aa38151aef50c0151aef50c2600cc',
			answerInfo:{},
			showNoData: false
		};
	},
	onLoad(options){
		if(options.id){
			// console.log(options.id)
			this.id = options.id
			this.collectsId = options.collectsId
		}
	},
	onShow(){
		if(this.id){
			this.getNumExaminationResult()
		}
	},
	methods:{
		// 获取答题结果数据
		getNumExaminationResult(){
			this.$Request.get('/appExaminationController.do?getNumExaminationResult',{
				id:this.id
			}).then(res => {
				if(res.code == 0){
					this.answerInfo = res.data
					if(this.answerInfo.lostList.length <= 0) {
						this.showNoData = true
					}else{
						this.showNoData = false
					}
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		// 重新测试
		goBackAll(){
			uni.navigateTo({
				url: '/pages/train/imageMemory/numImgAll'
			})
		},
		// 前往训练
		gotoTest(){
			uni.navigateTo({
				url: `/pages/train/imageMemory/numImgTest?collectsId=${this.collectsId}`
			})
		},
		// 回看过程
		goBackRead(){
			uni.navigateTo({
				url: '/pages/train/imageMemory/numImgAllRead'
			})
		},
		// 返回首页
		gotoHome(){
			uni.switchTab({
				url:'/pages/myData/myData'
			})
		}
	}
}
